@import url(https://fonts.googleapis.com/css?family=Monoton);
body {
  margin: 0;
  padding: 0;
  font-family: 'Monoton', cursive;
  background: url(/Star-Wars-API/static/media/star-wars-hd-background.f28e37ba.jpg) no-repeat center fixed;
  background-size: cover;
  color: #96ccff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.App {
    text-align: center;
}

.App-header {
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(12px + 2vmin);
    color: white;
}

.App-link {
    color: deepskyblue;
    text-decoration: none;
}


button {
    color: deepskyblue;
    font-weight: 900;
}
